<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <slot/>
    </div>
    <slot name="pagination"></slot>
    <div class="swiper-pagination" v-if="defaultPagination && !$slots.pagination"></div>
  </div>
</template>

<script>

// 
const eventList = [
  'beforeDestroy',
  'slideChange',
  'slideChangeTransitionStart',
  'slideChangeTransitionEnd',
  'slideNextTransitionStart',
  'slideNextTransitionEnd',
  'slidePrevTransitionStart',
  'slidePrevTransitionEnd',
  'transitionStart',
  'transitionEnd',
  'touchStart',
  'touchMove',
  'touchMoveOpposite',
  'sliderMove',
  'touchEnd',
  'click',
  'tap',
  'doubleTap',
  'imagesReady',
  'progress',
  'reachBeginning',
  'reachEnd',
  'fromEdge',
  'setTranslate',
  'setTransition',
  'resize',
  'observerUpdate'
]
/**
 * 
 */
export default {
  name:  'vui-swiper',
  props: {
    // 
    speed: {
      type: Number,
      default: 300
    },
    // 
    direction: {
      type: String,
      default: 'horizontal'
    },
    // true
    autoHeight: {
      type: Boolean,
      default: false
    },
    // true
    roundLengths: {
      type: Boolean,
      default: false
    },
    // 	Tranisition effect. Could be "slide", "fade", "cube", "coverflow" or "flip"
    effect: {
      type: String,
      default: 'slide'
    },
    // 
    watchOverflow: {
      type: Boolean,
      default: false
    },
    // --------- props -----------
    // 
    defaultPagination: {
      type: Boolean,
      default: true
    }
  },
  mounted() {
    this.initSwiper()
  },
  methods: {
    // 
    bindEvents () {
      eventList.forEach(item => {
        if (this.$listeners[item]) {
          this.swiper.on(item, () => {
            this.$emit(item)
          })
        }
      })
    },
    /**
     * 
     */
    initSwiper () {
      // 
      const parameter = {
        speed: this.speed,
        direction: this.direction,
        autoHeight: this.autoHeight,
        roundLengths: this.roundLengths,
        effect: this.effect,
        watchOverflow: this.watchOverflow
      }
      const pagination = this.initPagination()
      if (pagination !== null) {
        parameter['pagination'] = pagination
      }
      const Swiper = window['Swiper']
      if (!Swiper) {
        throw Error('Swiper')
      }
      this.swiper = new Swiper(this.$el, parameter)
      this.bindEvents()
    },
    // 
    initPagination () {
      if (this.$slots.pagination) {
        const paginationComponent = this.getPaginationComponent()
        if (paginationComponent !== null) {
          return paginationComponent.init()
        }
      }
      if (this.defaultPagination && !this.$slots.pagination) {
        return {
          el: '.swiper-pagination'
        }
      }
      return null
    },
    /**
     * 
     * @returns {null}
     */
    getPaginationComponent () {
      let component = null
      for (let item of this.$children) {
        if (item.$options.name === 'vui-swiper-pagination') {
          component = item
        }
      }
      return component
    }
  }
}
</script>

