<script>
/**
 * 
  */
export default {
  name: 'vui-radio',
  props: {
    label: {
      required: true
    },
    value: {
      /* eslint-disable */
      type: String | Number | Boolean
    }
  },
  computed: {
    computedChecked () {
      return this.label === this.computedModel
    },
    /**
     * radio
     */
    isInGroup () {
      let parent = this.$parent
      while (parent) {
        if (parent.$options.name === 'vui-radio-group') {
          this._radioGroup = parent
          return true
        } else {
          parent = parent.$parent
        }
      }
      return false
    },
    /**
     * 
     * @returns {default.props.value|{type}}
     */
    computedModel: {
      get () {
        return this.isInGroup ? this._radioGroup.value : this.value
      },
      set (value) {

        if (this.isInGroup) {
          this._radioGroup.$emit('input', value)
        } else {
          this.$emit('input', value)
        }
      }
    }
  },
  methods: {
    handleClick () {
      this.computedModel = this.label
    },
    createBaseTemplate (h) {
      return (
        <label onClick={this.handleClick} class="weui-cell weui-check__label">
          <div class="weui-cell__bd" >
            {this.$slots.default}
          </div>
          <div class="weui-cell__ft">
            <input type="radio" class="weui-check" checked={this.computedChecked}/>
            <span class="weui-icon-checked"></span>
          </div>
        </label>
      )
    }
  },
  render(h) {
    if (this.isInGroup) {
      return this.createBaseTemplate(h)
    } else {
      return <div class="weui-cells_radio">
      {this.createBaseTemplate(h)}
      </div>
    }
  }
}
</script>

