<template>
  <a @click="handleClick" href="javascript:" :class="computedClass">
    <i v-if="loading" class="weui-loading"></i>
    <slot/>
  </a>
</template>

<script>
/**
 * 
  */
export default {
  name: 'vui-button',
  props: {
    // 
    type: {
      type: String,
      default: 'default'
    },
    // 
    disabled: Boolean,
    // 
    loading: {
      type: Boolean,
      default: false
    },
    // 
    block: {
      type: Boolean,
      default: false
    },
    // 
    cell: {
      type: Boolean,
      default: false
    },
    // 
    plain: {
      type: Boolean,
      default: false
    },
    mini: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    computedClass () {
      const styleList = []
      styleList.push(this.getMainClass())
      styleList.push(this.getTypeClass())
      if (this.disabled === true) {
        styleList.push('weui-btn_disabled')
      }
      if (this.loading === true) {
        styleList.push('weui-btn_loading')
      }
      if (this.block === true) {
        styleList.push('weui-btn_block')
      }
      if (this.mini) {
        styleList.push('weui-btn_mini')
      }
      // 
      return styleList.join(' ')
    }
  },
  methods: {
    handleClick (event) {
      this.$emit('click', event)
    },
    getMainClass () {
      const cal = ['weui-btn']
      if (this.cell === true) {
        cal.push('cell')
      }
      return cal.join('_')
    },
    /**
     * 
     */
    getTypeClass () {
      const cal = ['weui-btn']
      if (this.cell === true) {
        cal.push('cell')
      }
      cal.push(this.type)
      return cal.join('_')
    },
    getPalinClass () {
      if (this.plain ===  true) {

      }
      return ''
    }
  }
}
</script>

